import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import defaultStyles from './_now-stylized-text.scss';

const view = ({
	// eslint-disable-next-line no-unused-vars
	properties: { tag: TAG, text, css }
}) => {
	const tagAndText = <TAG>{text}</TAG>;

	return css ? (
		<div>
			<style>{css}</style>
			{tagAndText}
		</div>
	) : (
		tagAndText
	);
};

createCustomElement('now-stylized-text', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		tag: {
			default: 'h1'
		},
		text: {
			default: ''
		},
		css: {
			default: ''
		}
	},
	styles: defaultStyles
});
